import React from 'react';
import './Adminlogin.css';
import Avatar from '@material-ui/core/Avatar';
import Person from '../../assets/img/person.png';



class AdminLogin extends React.Component {
    constructor(props) {
        super(props);
        this.state = {}
    }
    render() {
        return (
            <React.Fragment>
                    <div className="LJtPoc">
                        <div className="bdf4dc">
                            <div className="SSBsw">
                            <Avatar alt="Default Profile" src={Person}>
                                A
                            </Avatar>
                            </div>
                            <div>
                                <h1 className="sfYUmb">Sign in</h1>
                                <p className="FgbZLd">with your Admin Account</p>
                            </div>
                            <div>
                                <div className="place">
                                    <input type="text" placeholder="Admin ID" name="adminid" required />
                                </div>
                                <div className="place">
                                    <input type="password" placeholder="Admin Password" name="adminpassword" required />
                                </div>
                                    <div className="lala">
                                        <button type="button" className="edit_button">NEXT</button>
                                    </div>
                                </div>
                            </div>
                        </div>
            </React.Fragment>
                );
           }
       }
        
export default AdminLogin;