import React from 'react';
import clsx from 'clsx';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import CssBaseline from '@material-ui/core/CssBaseline';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import DoubleArrowIcon from '@material-ui/icons/DoubleArrow';
import Avatar from '@material-ui/core/Avatar';
import Grid from '@material-ui/core/Grid';
import SecurityIcon from '@material-ui/icons/Security';
import PersonPinIcon from '@material-ui/icons/PersonPin';
import SettingsApplicationsIcon from '@material-ui/icons/SettingsApplications';
import InfoIcon from '@material-ui/icons/Info';
import ContactPhoneIcon from '@material-ui/icons/ContactPhone';
import NewReleasesIcon from '@material-ui/icons/NewReleases';
import CardMembershipIcon from '@material-ui/icons/CardMembership';
import { deepOrange } from '@material-ui/core/colors';
import Person from '../../assets/img/person.png';
import TextField from '@material-ui/core/TextField';
import getUserData from "../../rest/UserREST.js";
import { withRouter } from 'react-router-dom';


const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
    root: {
        minWidth: 275,
    },
    cardroot: {
        width: 295
    },
    formroot: {
        '& > *': {
          margin: theme.spacing(1),
          width: '25ch',
        },
      },
    bullet: {
        display: 'inline-block',
        margin: '0 2px',
        transform: 'scale(0.8)',
    },
    Login: {
        marginTop: '5px',
        background: '#286efa !important',
        width: '120px',
        borderRadius: '3px',
        color: '#fff'
    },
    title: {
        fontSize: 14,
    },
    pos: {
        marginBottom: 12,
    },
    root: {
        display: 'flex',
    },
    orange: {
        color: theme.palette.getContrastText(deepOrange[500]),
        backgroundColor: deepOrange[500],
        width: '80px',
        height: '80px'
    },
    profileDetails: {
        listStyle: 'none',
        textAlign: 'center',
        marginRight: '40px'
    },
    vertical_center: {
        marginTop: '20%'
    },
    skip: {
        marginTop: '5px',
        background: '#286efa !important',
        width: '120px',
        borderRadius: '3px',
        color: '#fff'
    },
    userProfileimg: {
        justifyContent: 'center',
    },
    userEmail:{
        width: '100%',
        padding: '12px 10px',
        display: 'inline-block',
        border: 'none',
        boxSizing: 'border-box'
    },
    EmailTextfield: {
        justifyContent: 'center',
        marginTop: '10px'
    },
    signin_btn: {
        justifyContent: 'center',
        marginTop: '10px'
    },
    close_drawer: {
        transform: 'scaleX(-1)',
        cursor: 'pointer',
    },
    double_arrow: {
        cursor: 'pointer',
    },
    appBar: {
        zIndex: theme.zIndex.drawer + 1,
        transition: theme.transitions.create(['width', 'margin'], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
    },
    appBarShift: {
        marginLeft: drawerWidth,
        width: `calc(100% - ${drawerWidth}px)`,
        transition: theme.transitions.create(['width', 'margin'], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    menuButton: {
        marginRight: 36,
    },
    hide: {
        display: 'none',
    },
    drawer: {
        width: drawerWidth,
        flexShrink: 0,
        whiteSpace: 'nowrap',
    },
    drawerOpen: {
        width: drawerWidth,
        transition: theme.transitions.create('width', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    drawerClose: {
        transition: theme.transitions.create('width', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
        overflowX: 'hidden',
        width: theme.spacing(7) + 1,
        [theme.breakpoints.up('sm')]: {
            width: theme.spacing(9) + 1,
        },
    },
    toolbar: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        padding: theme.spacing(0, 1),
        // necessary for content to be below app bar
        ...theme.mixins.toolbar,
    },
    content: {
        flexGrow: 1,
        padding: theme.spacing(3),
    }
}));

const Login = (props) => {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);
    const [isopen_drawer, setDrawer] = React.useState(true);
    const [isEmpty, setisEmpty] = React.useState(false);
    const [userEmailID, setuserEmailID] = React.useState('');


    const handleDrawer = () => {
        (open) ? setOpen(false) : setOpen(true);
        (isopen_drawer) ? setDrawer(false) : setDrawer(true);
    }
    
    const userEmail = (e) =>{
        setuserEmailID(e.target.value)
        console.log('User email',userEmailID)
    }

    const LoginUser = () =>{
        if(userEmailID == ''){
            setisEmpty(true)
        }
        let api_url = "api/users/login";
        let data = {
            email: userEmailID,
        }
          getUserData
            .postUserdetails(api_url, data)
            .then(response => {
                props.history.push('/Home')
                console.log("Response Data...", response);
            }).catch((err) =>{
                console.log("Error Data...", err);
            })
    }
    return (
        <React.Fragment>
            <CssBaseline />
            <Drawer
                variant="permanent"
                className={clsx(classes.drawer, {
                    [classes.drawerOpen]: open,
                    [classes.drawerClose]: !open,
                })}
                classes={{
                    paper: clsx({
                        [classes.drawerOpen]: open,
                        [classes.drawerClose]: !open,
                    }),
                }}
            >
                <div className={classes.toolbar}>
                    <Typography variant="h6" gutterBottom >
                        {
                            (open) ? <span>Safe Basket</span> : <span>SB</span>
                        }
                    </Typography>
                </div>
                <Divider />
                <List>
                    <ListItem button>
                        <ListItemIcon><InfoIcon /></ListItemIcon>
                        <ListItemText primary='About Us' />
                    </ListItem>
                    <ListItem button>
                        <ListItemIcon><ContactPhoneIcon /></ListItemIcon>
                        <ListItemText primary='Contact Us' />
                    </ListItem>
                    <ListItem button>
                        <ListItemIcon><NewReleasesIcon /></ListItemIcon>
                        <ListItemText primary='App Features' />
                    </ListItem>
                    <ListItem button>
                        <ListItemIcon><CardMembershipIcon /></ListItemIcon>
                        <ListItemText primary='Credits' />
                    </ListItem>
                    <ListItem button>
                        <ListItemIcon><SettingsApplicationsIcon /></ListItemIcon>
                        <ListItemText primary='Settings' />
                    </ListItem>
                </List>
                <Divider />
                <List>
                    <ListItem className={(isopen_drawer) ? classes.double_arrow : classes.close_drawer} onClick={handleDrawer}>
                        <ListItemIcon>
                            <DoubleArrowIcon />
                        </ListItemIcon>
                    </ListItem>
                </List>
                <Divider />
            </Drawer>
            <main className={classes.content}>
                <Grid
                    className={classes.vertical_center}
                    container
                    direction="row"
                    justify="center"
                    alignItems="center"
                >
                    <Grid container item xs={12} sm={12} md={12} lg={12} xl={12} className={classes.userProfileimg}>
                        <Card className={classes.cardroot} elevation={3}>
                            <CardContent>
                                <Grid container item xs={12} sm={12} md={12} lg={12} xl={12} className={classes.userProfileimg}>
                                    <Avatar alt="Default Profile" src={Person} className={classes.orange}>
                                        P
                                    </Avatar>
                                </Grid>
                                <Grid container item xs={12} sm={12} md={12} lg={12} xl={12} className={classes.EmailTextfield}>
                                    <form className={classes.formroot} noValidate autoComplete="off">
                                    <TextField error={isEmpty} onChange={e=>userEmail(e)} id="outlined-search" label="Email" type="search" variant="outlined" />
                                    </form>
                                </Grid>
                                <Grid container item xs={12} sm={12} md={12} lg={12} xl={12} className={classes.signin_btn}>
                                    <Button className={classes.Login} onClick={LoginUser}>Login</Button>
                                </Grid>
                            </CardContent>
                        </Card>
                    </Grid>
                </Grid>
            </main>
        </React.Fragment>
    );
}

export default withRouter(Login);
